import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import boatType from 'components/__types__/boat';
import BoatFieldTable from 'components/boat-field-table/BoatFieldTable';

const BoatDetails = ({ t, boat }) => {
  return (
    <Box>
      <h3 data-testid="details_title">
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Boat',
        })}
      </h3>
      <BoatFieldTable boat={boat} />
    </Box>
  );
};

BoatDetails.propTypes = {
  boat: boatType,
  t: PropTypes.func.isRequired,
};

BoatDetails.defaultProps = {
  boat: {},
};

export default withTranslation()(BoatDetails);
