import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import boatType from 'components/__types__/boat';

const BoatFieldTable = ({ t, boat }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>{t('common.name')}</TableCell>
        <TableCell>{t('common.value')}</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      <TableRow>
        <TableCell>
          <span>{t('entities.boat.id')}</span>
        </TableCell>
        <TableCell data-testid="boatIdValue">
          <span>{boat.id}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.boat.name')}</span>
        </TableCell>
        <TableCell>
          <span>{boat.name}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.boat.price')}</span>
        </TableCell>
        <TableCell>
          <span>{boat.price}</span>
        </TableCell>
      </TableRow>
    </TableBody>
  </Table>
);

BoatFieldTable.propTypes = {
  boat: boatType,
  t: PropTypes.func.isRequired,
};

BoatFieldTable.defaultProps = {
  boat: [],
};

export default withTranslation()(BoatFieldTable);
